import React from 'react';
import '../assets/styles/my-image.scss';
export default class MyImageComponent extends React.Component{

  render(){
    let {props:{toggleAboutMe}} = this;
    return (
      <div className="MyImageComponent" style={{backgroundImage: `url(${require('../assets/png/lauta.png')}),linear-gradient(to top right, #165479, #00a2ff)`}}>
        <button className="btn about-me" onClick={()=>toggleAboutMe(true)}>
          About Me
        </button>
      </div>
    );
  }
}
